
.btn {
    padding: 10px 20px;
    font-size: 14px;
    letter-spacing: 0.5px;
    transition: all 0.5s;
    font-weight: 400;
    line-height: 1.5;
    border-radius: 0.25rem;
    border: 1px solid transparent;
    background-color: var(--primary-color);
    transition: all .3s .05s;
    cursor: pointer;
}
.btn:hover{
    transform: translateY(-5px);
}